import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { PostsService } from 'src/app/services/posts.service';
import { LoadingController, NavController } from '@ionic/angular';
import { catchError } from 'rxjs/operators';
import Swal from 'sweetalert2';
import { IRequestResponse } from 'src/app/models/response.model';
import { IUsers } from 'src/app/models/users.model';
import { SubjectsService } from 'src/app/services/subjects';
@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  public showPasswordIcon = 'eye-off-outline';
  public passwordInputType = 'password';
  loginForm: FormGroup = this.formBuilder.group({
    username: ['', Validators.required],
    password: ['', Validators.required],
  });

  constructor(
    private formBuilder: FormBuilder,
    private postServices: PostsService,
    private subjects: SubjectsService,
    private navCtrl: NavController,
    private loadingCtrl: LoadingController
  ) {}

  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
    });
  }

  async onSubmit() {
    if (!this.loginForm.valid) {
      Swal.fire({
        icon: 'info',
        title: 'Complete usuario y clave',
        text: 'El usuario puede ser su correo o nÃºmero de celular',
      });
      return;
    }
    const loading = await this.loadingCtrl.create({
      message: 'Loading...',
      duration: 0,
      cssClass: 'custom-loading',
    });

    loading.present();
    this.postServices
      .initSession(
        this.loginForm.get('username').value,
        this.loginForm.get('password').value
      )
      .pipe(
        catchError((error) => {
          Swal.fire({
            icon: 'error',
            title: 'Error del servidor',
          });
          loading.dismiss();
          throw { ...error };
        })
      )
      .subscribe((response: IRequestResponse<IUsers>) => {
        loading.dismiss();
        if (!response.result) {
          Swal.fire({
            icon: 'warning',
            title: 'Datos incorrectos',
            text: response.message,
          });
          return;
        }

        this.subjects.user$.next(response.data);

        const sessionEncrypted = btoa(JSON.stringify(response.data));
        localStorage.setItem('token', sessionEncrypted);
        this.navCtrl.navigateForward('/home/schedule');
      });
  }

  forgotPassword() {
    Swal.fire({
      icon: 'warning',
      title: 'NO DISPONIBLE',
      text: 'Me encuentro trabajando en el despacho de correos, si necesitas una nueva clave, por favor contactete con el administrador',
    });
    /* Swal.fire({
      title: 'Coloque su usuario',
      input: 'text',
      inputAttributes: {
        autocapitalize: 'off',
      },
      showCancelButton: true,
      confirmButtonText: 'Look up',
      showLoaderOnConfirm: true,
      preConfirm: (login) => {
        return fetch(`//api.github.com/users/${login}`)
          .then((response) => {
            if (!response.ok) {
              throw new Error(response.statusText);
            }
            return response.json();
          })
          .catch((error) => {
            Swal.showValidationMessage(`Request failed: ${error}`);
          });
      },
      allowOutsideClick: () => !Swal.isLoading(),
    }).then((result) => {
      if (result.isConfirmed) {
        Swal.fire({
          title: `${result.value.login}'s avatar`,
          imageUrl: result.value.avatar_url,
        });
      }
    }); */
  }

  public showPassword(): void {
    if (this.passwordInputType === 'password') {
      this.passwordInputType = 'text';
      this.showPasswordIcon = 'eye-outline';
      return;
    }
    this.passwordInputType = 'password';
    this.showPasswordIcon = 'eye-off-outline';
    return;
  }
}
